export default {
	props: {
		size: { //星星的大小
			type: [Number, String],
			default: 36
		},
		value: { //当前评分
			type: [Number, String],
			default: 5
		}
	},
	data() {
		return {
			startURL: this.$url.staticURL + 'scanAll/star-on.png',
			defaultURL: this.$url.staticURL + 'scanAll/star-off.png',
			startMax: 5
		}
	},
	created() {
	},
	methods: {
		changeStar(index) {
			this.$emit('changeStar', {
				num: index
			})
		}
	},
	computed: {
		halfWidth() {
			return uni.upx2px(this.size) + 'px'
		}
	}
}
